<template>
  <v-container>
    <div>
      <v-card tile class="mx-auto">
        <v-img
          position="bottom"
          class="white--text"
          height="70px"
          src="@/assets/card-bg.jpg"
        >
          <v-card-text class="text-right text-h6 font-weight-black">
            USER LIST
          </v-card-text>
        </v-img>
      </v-card>

      <v-data-table
        dense
        :headers="headers"
        :items="members"
        :search="search"
        class="elevation-1"
      >
        <template v-slot:top>
          <v-toolbar flat>
            <v-dialog v-model="dialog" max-width="500px">
              <template v-slot:activator="{ on: dialog, attrs }">
                <v-tooltip bottom>
                  <template v-slot:activator="{ on: tooltip }">
                    <v-btn
                      fab
                      small
                      color="primary"
                      dark
                      v-bind="attrs"
                      v-on="{ ...tooltip, ...dialog }"
                    >
                      <v-icon>mdi-plus</v-icon>
                    </v-btn>
                  </template>
                  <span>ADD USER</span>
                </v-tooltip>
              </template>

              <v-card>
                <v-card-title>
                  <span class="text-h5">{{ formTitle }}</span>
                </v-card-title>

                <v-card-text>
                  <v-container>
                    <v-row>
                      <!-- <v-col cols="12" sm="6" md="4"> -->
                      <v-row>
                        <v-col cols="12" md="6">
                          <v-text-field
                            v-model="editedItem.username"
                            :rules="[(v) => !!v || 'Name is required']"
                            label="Name"
                            required
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" md="6">
                          <v-text-field
                            v-model="editedItem.email"
                            :rules="editedItem.emailRules"
                            label="Email"
                            required
                            type="email"
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" md="6">
                          <v-text-field
                            v-model="editedItem.password"
                            :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                            :rules="[(v) => !!v || 'Password is required']"
                            :type="show1 ? 'text' : 'password'"
                            label="Password"
                            required
                            counter
                            @click:append="show1 = !show1"
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" md="6">
                          <v-select
                            dense
                            v-model="editedItem.roles"
                            :items="access"
                            item-text="name"
                            item-value="roleCode"
                            :menu-props="{ maxHeight: '400' }"
                            label="USER LEVEL"
                            hint="Set the user access"
                            persistent-hint
                            multiple
                          ></v-select>
                        </v-col>
                        <v-col cols="12" md="6">
                          <v-select
                            dense
                            v-model="editedItem.branch"
                            :items="branch"
                            item-text="name"
                            item-value="brCode"
                            :menu-props="{ maxHeight: '400' }"
                            label="BRANCH"
                            hint="Set the user branch"
                            persistent-hint
                          ></v-select>
                        </v-col>
                        <v-col cols="12" md="6">
                          <v-select
                            dense
                            v-model="editedItem.dept"
                            :items="dept"
                            item-text="name"
                            item-value="name"
                            :menu-props="{ maxHeight: '400' }"
                            label="DEPARTMENT"
                            hint="Set the user department"
                            persistent-hint
                          ></v-select>
                        </v-col>
                      </v-row>
                    </v-row>
                  </v-container>
                </v-card-text>

                <v-card-actions>
                  <v-checkbox
                    v-model="editedItem.active"
                    :label="`Active`"
                  ></v-checkbox>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="close">
                    Cancel
                  </v-btn>
                  <v-btn
                    :disabled="isAddButtonDisabled"
                    color="blue darken-1"
                    text
                    @click="save"
                  >
                    Save
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>

            <v-divider class="mx-4" inset vertical></v-divider>
            <v-dialog v-model="dialogDelete" max-width="500px">
              <v-card>
                <v-card-title class="text-h5"
                  >Are you sure you want to delete
                  {{ editedItem.username }}?</v-card-title
                >
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="blue darken-1" text @click="closeDelete"
                    >Cancel</v-btn
                  >
                  <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                    >OK</v-btn
                  >
                  <v-spacer></v-spacer>
                </v-card-actions>
              </v-card>
            </v-dialog>

            <v-spacer></v-spacer>
            <v-text-field
              v-model="search"
              append-icon="mdi-magnify"
              label="Search"
              single-line
              hide-details
              clearable
            ></v-text-field>
          </v-toolbar>
        </template>

        <template slot="item.numbering" slot-scope="props">
          {{ props.index + 1 }}
        </template>

        <template v-slot:[`item.active`]="{ item }">
          <v-chip :color="getStatusColor(item.active)" dark small>
            <tr>
              {{
                item.active.toLowerCase()
              }}
            </tr>
          </v-chip>
        </template>

        <template v-slot:[`item.roles`]="{ item }">
          <span
            v-for="allRoles in item.roles"
            :key="allRoles.id"
            v-bind="allRoles.item"
          >
            <!-- <v-chip :color="getRolesColor(item.roles)" dark small> -->
            <v-chip
              :color="
                allRoles.name === 'ADMIN'
                  ? 'red'
                  : allRoles.name === 'MANAGER'
                  ? 'indigo'
                  : allRoles.name === 'BRANCH MANAGER'
                  ? 'blue'
                  : allRoles.name === 'ADJUSTER'
                  ? 'purple'
                  : allRoles.name === 'CLERK'
                  ? 'green'
                  : allRoles.name === 'BRANCH CLERK'
                  ? 'pink lighten-3'
                  : allRoles.name === 'EDITOR'
                  ? 'brown lighten-1'
                  : allRoles.name === 'DIRECTOR'
                  ? 'blue-grey darken-1'
                  : allRoles.name === 'STAFF'
                  ? 'orange'
                  : 'black'
              "
              dark
              x-small
            >
              <!-- <div v-for="oneRole in allRoles.name" :key="oneRole.id"> -->
              {{ allRoles.name.toLowerCase() }}
              <!-- </div> -->
            </v-chip>
          </span>
        </template>

        <!-- Trick to truncate text and show elipsis at the end  -->
        <!-- <template v-slot:[`item.name`]="{ value }">
          <div class="text-truncate" style="max-width: 40px">
            {{ value }}
          </div>
        </template> -->

        <template v-slot:[`item.actions`]="{ item }">
          <v-tooltip left transition="fab-transition">
            <template v-slot:activator="{ on, attrs }">
              <v-icon
                small
                v-bind="attrs"
                v-on="on"
                class="mr-2"
                @click="editItem(item)"
              >
                mdi-pencil
              </v-icon>
            </template>
            <span> edit</span>
          </v-tooltip>
          <v-tooltip right transition="fab-transition">
            <template v-slot:activator="{ on, attrs }">
              <v-icon
                small
                v-bind="attrs"
                v-on="on"
                color="red darken-1"
                @click="deleteItem(item)"
              >
                mdi-delete
              </v-icon>
            </template>
            <span> delete</span>
          </v-tooltip>
        </template>
      </v-data-table>
    </div>
  </v-container>
</template>

<script>
import MemberDataService from "../../services/MemberDataService";
import AccessDataService from "../../services/AccessDataService";
import BranchDataService from "../../services/BranchDataService";
import DeptDataService from "../../services/DeptDataService";

export default {
  data: () => ({
    access: [],
    branch: [],
    dept: [],

    dialog: false,
    dialogDelete: false,
    members: [],
    search: "",
    show1: false,

    defaultItem: {
      id: null,
      username: "",
      email: "",
      emailRules: [
        (v) => !!v || "E-mail is required",
        (v) => /.+@.+/.test(v) || "E-mail must be valid",
      ],
      password: "",
      active: "",
      roles: "",
      branch: "",
      dept: "",
    },

    editedIndex: -1,

    editedItem: {
      id: null,
      username: "",
      email: "",
      emailRules: [
        (v) => !!v || "E-mail is required",
        (v) => /.+@.+/.test(v) || "E-mail must be valid",
      ],
      password: "",
      active: "",
      roles: "",
      branch: "",
      dept: "",
    },

    headers: [
      {
        text: "NO.",
        align: "start",
        sortable: false,
        value: "numbering",
        // class: "blue lighten-5",
      },
      {
        text: "NAME",
        align: "start",
        sortable: false,
        value: "username",
        // class: "blue lighten-5",
      },
      {
        text: "EMAIL",
        value: "email",
        sortable: false,
        // class: "blue lighten-5",
      },
      {
        text: "DEPARTMENT",
        align: "center",
        value: "dept",
        sortable: false,
        // class: "blue lighten-5",
      },
      {
        text: "BRANCH",
        align: "center",
        value: "branch",
        sortable: false,
        // class: "blue lighten-5",
      },
      {
        text: "STATUS",
        align: "center",
        value: "active",
        sortable: false,
        // class: "blue lighten-5",
      },
      {
        text: "ACCESS",
        align: "center",
        value: "roles",
        sortable: false,
        // class: "blue lighten-5",
      },
      {
        text: "ACTIONS",
        align: "center",
        value: "actions",
        sortable: false,
        // class: "blue lighten-5",
      },
    ],
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "New Item" : "Edit Item";
    },
    isAddButtonDisabled() {
      return !(
        this.editedItem.username &&
        this.editedItem.email &&
        this.editedItem.password &&
        this.editedItem.roles &&
        this.editedItem.branch &&
        this.editedItem.dept
      );
    },

    // memberRoles: function() {
    //   // return this.members.filter(function(item) {
    //   //   return item.id == 2;
    //   // });

    //   return this.members.filter((item) => [2].includes(item.id));
    // },

    // One condition filter by branch
    // filteredMembers: function() {
    //   return this.members.filter((member) => {
    //     // return member.branch.match("KEL");
    //     return member.branch.match(this.$store.state.auth.user.branch);
    //   });
    // },

    // Two condition filter by branch
    // filteredMembers: function() {
    //   return this.members.filter((member) => {
    //     // return member.branch.match("KEL");

    //     return (
    //       member.branch == this.$store.state.auth.user.branch &&
    //       member.dept == this.$store.state.auth.user.dept
    //     );
    //   });
    // },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  methods: {
    retrieveAccess() {
      AccessDataService.getAll()
        .then((response) => {
          this.access = response.data.map(this.getDisplayAccess);
          // console.log(response.data);
        })
        .catch((e) => {
          console.log(e);
        });
    },

    getDisplayAccess(role) {
      return {
        id: role.id,
        name: role.name,
        roleCode: role.roleCode,
      };
    },

    retrieveBranch() {
      BranchDataService.getAll()
        .then((response) => {
          this.branch = response.data;
          // console.log(response.data);
        })
        .catch((e) => {
          console.log(e);
        });
    },

    retrieveDept() {
      DeptDataService.getAll()
        .then((response) => {
          this.dept = response.data;
          // console.log(response.data);
        })
        .catch((e) => {
          console.log(e);
        });
    },

    retrieveMembers() {
      MemberDataService.getAll()
        .then((response) => {
          this.members = response.data.map(this.getDisplayMember);
        })
        .catch((e) => {
          console.log(e);
        });
    },

    refreshList() {
      this.retrieveMembers();
    },

    getRolesColor(roles) {
      if (roles[0].name == "ADMIN") return "red";
      else if (roles[0].name == "MANAGER") return "orange";
      else if (roles[0].name == "BRANCH MANAGER") return "indigo";
      else return "grey";
    },

    getStatusColor(active) {
      if (active == "Inactive") return "green lighten-2";
      else return "green darken-3";
    },

    // retrieveRoles() {
    //   MemberDataService.getAllRoles()
    //     .then((response) => {
    //       this.userLevel = response.data;
    //       console.log(response.data);
    //     })
    //     .catch((e) => {
    //       console.log(e);
    //     });
    // },

    // filters() {
    //   return this.data.members(
    //     (item) => item[0].roles[0].user_roles.userId === "members"
    //   );
    // },

    getDisplayMember(member) {
      return {
        id: member.id,
        username:
          member.username.length > 30
            ? member.username.substr(0, 30) + "..."
            : member.username,
        email:
          member.email.length > 30
            ? member.email.substr(0, 30) + "..."
            : member.email,
        active: member.active ? "Active" : "Inactive",
        roles: member.roles,
        branch: member.branch,
        dept: member.dept,
      };
    },

    editItem(item) {
      this.editedIndex = this.members.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.members.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      MemberDataService.delete(this.editedItem.id)
        .then((response) => {
          this.$store.dispatch("setSnackbar", {
            showing: true,
            color: "error",
            text:
              (response && response.data && response.data.message) ||
              toString(),
          });

          this.refreshList();
        })
        .catch((e) => {
          console.log(e);
        });

      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    save() {
      if (this.editedIndex > -1) {
        MemberDataService.update(this.editedItem.id, this.editedItem)
          .then((response) => {
            this.close();
            this.$store.dispatch("setSnackbar", {
              showing: true,
              color: "warning",
              text:
                (response && response.data && response.data.message) ||
                toString(),
            });
            this.refreshList();
          })
          .catch((e) => {
            console.log(e);
          });
      } else {
        var data = {
          username: this.editedItem.username,
          email: this.editedItem.email,
          password: this.editedItem.password,
          active: this.editedItem.active,
          roles: this.editedItem.roles,
          branch: this.editedItem.branch,
          dept: this.editedItem.dept,
        };

        MemberDataService.create(data)
          .then((response) => {
            this.editedItem.id = response.data.id;
            this.close();
            this.$store.dispatch("setSnackbar", {
              showing: true,
              color: "success",
              text:
                (response && response.data && response.data.message) ||
                toString(),
            });
            this.refreshList();
          })
          .catch((e) => {
            this.$store.dispatch("setSnackbar", {
              showing: true,
              text:
                (e.response && e.response.data && e.response.data.message) ||
                e.message ||
                toString(),
            });
          });
      }
      this.close();
    },
  },
  mounted() {
    this.retrieveMembers();
    this.retrieveAccess();
    this.retrieveBranch();
    this.retrieveDept();
  },
};
</script>
